// src/router/index.js
import { createRouter, createWebHashHistory } from 'vue-router' //  createWebHashHistory
import Homepage from '@/components/Homepage.vue'
import aiznfk from '@/components/aiznfk.vue'
import Aboutus from '@/components/aboutus.vue'
import Login from '@/components/login.vue'
import Opinion from '@/components/opinion.vue'
import AR from '@/components/AR.vue'
import Flex from '@/components/flex.vue'
import MerchantRank from '@/components/MerchantRank.vue'
import Aifirend from '@/components/aifirend.vue'
import DataCockpit from '@/components/DataCockpit.vue'
import MembershipView from '@/components/slides/MembershipView.vue'
const routes = [
  { path: '/', component: Homepage },
    { path: '/homepage', component: Homepage },
    { path: '/aiznfk', component: aiznfk },     //ai
        { path: '/about', component: Aboutus },   //
                { path: '/login', component: Login },  ///

                { path: '/opinion', component: Opinion },  //
                { path: '/ar', component: AR },    //ar
                                { path: '/flex', component: Flex },   //
                                { path: '/merchant-rank', component: MerchantRank },   //
                                { path: '/aifriend', component: Aifirend },   //ai
                                                                { path: '/aifriend', component: Aifirend },   //ai
                                                                { path: '/data-cockpit', component: DataCockpit },   //

                                                                { path: '/MembershipView', component: MembershipView },   //



  // ...
]

const router = createRouter({
  history: createWebHashHistory(), //  Hash 
  routes
})

export default router