        </div>
        
        <div class="modal-body">
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <div class="tag-group">
              <span 
                v-for="tag in currentJob.tags" 
                :key="tag.id"
                class="tag"
                :style="{ backgroundColor: tag.color }"
              >
                {{ tag.name }}
              </span>
            </div>
          </div>
          
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <p class="location-detail">
              <i class="icon-location"></i> {{ currentJob.locationDetail }}
            </p>
            <p class="distance">
              <i class="icon-distance"></i>  {{ currentJob.distance }} 
            </p>
          </div>
          
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <p class="description">{{ currentJob.description }}</p>
          </div>
          
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <p class="requirement">{{ currentJob.requirement }}</p>
          </div>
          
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <p class="welfare">{{ currentJob.welfare }}</p>
          </div>
          
          <!--  -->
          <div class="section">
            <h4 class="section-title"></h4>
            <p class="contact">{{ currentJob.contact }}</p>
          </div>
        </div>
        
        <div class="modal-footer">
          <button 
            class="collect-btn" 
            :class="{ 'collected': currentJob.collected }"
            @click="toggleCollect"
          >
            <i class="icon-star"></i> {{ currentJob.collected ? '' : '' }}
          </button>
          <button class="apply-btn" @click="applyJob"></button>
        </div>
      </div>
    </div>
    
    <!--  -->
    <div v-if="showToast" class="toast">
      {{ toastMessage }}
    </div>
  </teleport>
</template>

<script setup>
import { ref, computed } from 'vue';

// 
const categories = ref([
  { id: 'all', name: '', active: true },
  { id: 'food', name: '', active: false, text: '' },
  { id: 'retail', name: '', active: false, text: '' },
  { id: 'service', name: '', active: false, text: '' },
  { id: 'tech', name: '', active: false, text: '' },
  { id: 'design', name: '', active: false, text: '' },
  { id: 'marketing', name: '', active: false, text: '' },
  { id: 'activity', name: '', active: false, text: '' },
  { id: 'admin', name: '', active: false, text: '' }
]);

// 
const salaries = ref([
  { id: 'all', name: '', active: true },
  { id: 'below200', name: '200', active: false },
  { id: '200-300', name: '200-300', active: false },
  { id: '300-400', name: '300-400', active: false },
  { id: 'above400', name: '400', active: false }
]);

// 
const sorts = ref([
  { id: 'latest', name: '', active: true },
  { id: 'salaryHigh', name: '', active: false },
  { id: 'salaryLow', name: '', active: false },
  { id: 'distance', name: '', active: false }
]);

// 
const jobs = ref([
  {
    id: 1,
    title: '',
    company: '',
    salary: '180-220/',
    location: '',
    locationDetail: '1665',
    publishDate: '2025-11-17',
    distance: '0.8',
    tags: [
      { id: 1, name: '', color: '#52c41a' },
      { id: 2, name: '', color: '#fa8c16' },
      { id: 3, name: '', color: '#1890ff' },
      { id: 4, name: '', color: '#faad14' },
      { id: 5, name: '', color: '#f5222d' }
    ],
    category: 'food',
    categoryText: '',
    description: '',
    requirement: '18-30',
    welfare: '',
    contact: ' 13812345678',
    collected: false
  },
  {
    id: 2,
    title: '',
    company: '',
    salary: '350-400/',
    location: '',
    locationDetail: '16',
    publishDate: '2025-11-16',
    distance: '2.3',
    tags: [
      { id: 6, name: '', color: '#722ed1' },
      { id: 7, name: '', color: '#389e0d' },
      { id: 8, name: '', color: '#13c2c2' },
      { id: 9, name: '', color: '#eb2f96' }
    ],
    category: 'tech',
    categoryText: '',
    description: '',
    requirement: '20-452',
    welfare: '',
    contact: ' 13987654321',
    collected: false
  },
  {
    id: 3,
    title: '',
    company: '',
    salary: '250-300/',
    location: '',
    locationDetail: '15153',
    publishDate: '2025-11-18',
    distance: '3.5',
    tags: [
      { id: 10, name: '', color: '#0084ff' },
      { id: 11, name: '', color: '#8c8c8c' },
      { id: 12, name: '', color: '#faad14' },
      { id: 13, name: '', color: '#f5222d' }
    ],
    category: 'design',
    categoryText: '',
    description: '',
    requirement: 'PSAI',
    welfare: '',
    contact: ' 13765432109',
    collected: false
  },
  {
    id: 4,
    title: '',
    company: '',
    salary: '200-250/',
    location: '',
    locationDetail: '6002',
    publishDate: '2025-11-15',
    distance: '4.2',
    tags: [
      { id: 14, name: '', color: '#1890ff' },
      { id: 15, name: '', color: '#52c41a' },
      { id: 16, name: '', color: '#fa8c16' },
      { id: 17, name: '', color: '#13c2c2' }
    ],
    category: 'activity',
    categoryText: '',
    description: '',
    requirement: '3',
    welfare: '',
    contact: ' 13698765432',
    collected: false
  },
  {
    id: 5,
    title: '',
    company: 'H&M',
    salary: '220-260/',
    location: '',
    locationDetail: '600B1',
    publishDate: '2025-11-14',
    distance: '1.5',
    tags: [
      { id: 18, name: '', color: '#ff7d00' },
      { id: 19, name: '', color: '#faad14' },
      { id: 20, name: '', color: '#1890ff' },
      { id: 21, name: '', color: '#faad14' }
    ],
    category: 'retail',
    categoryText: '',
    description: '',
    requirement: '18-35',
    welfare: '',
    contact: ' 13587654321',
    collected: false
  },
  {
    id: 6,
    title: '',
    company: '',
    salary: '200-240/',
    location: '',
    locationDetail: '1684',
    publishDate: '2025-11-13',
    distance: '0.5',
    tags: [
      { id: 22, name: '', color: '#722ed1' },
      { id: 23, name: '', color: '#1890ff' },
      { id: 24, name: '', color: '#13c2c2' },
      { id: 25, name: '', color: '#fa8c16' }
    ],
    category: 'service',
    categoryText: '',
    description: '',
    requirement: '18-30',
    welfare: '',
    contact: ' 13476543210',
    collected: false
  }
]);

// 
const activeCategory = ref('all');
const activeSalary = ref('all');
const activeSort = ref('latest');

// 
const toggleCategory = (categoryId) => {
  activeCategory.value = categoryId;
  categories.value.forEach(cat => {
    cat.active = cat.id === categoryId;
  });
};

// 
const toggleSalary = (salaryId) => {
  activeSalary.value = salaryId;
  salaries.value.forEach(sal => {
    sal.active = sal.id === salaryId;
  });
};

// 
const toggleSort = (sortId) => {
  activeSort.value = sortId;
  sorts.value.forEach(srt => {
    srt.active = srt.id === sortId;
  });
};

// 
const filteredJobs = computed(() => {
  // 1. 
  let result = jobs.value.filter(job => {
    // 
    if (activeCategory.value !== 'all' && job.category !== activeCategory.value) {
      return false;
    }
    
    // 
    if (activeSalary.value !== 'all') {
      const salaryRange = job.salary.match(/\d+/g).map(Number);
      const minSalary = salaryRange[0];
      
      switch(activeSalary.value) {
        case 'below200':
          if (minSalary >= 200) return false;
          break;
        case '200-300':
          if (minSalary < 200 || minSalary >= 300) return false;
          break;
        case '300-400':
          if (minSalary < 300 || minSalary >= 400) return false;
          break;
        case 'above400':
          if (minSalary < 400) return false;
          break;
      }
    }
    
    return true;
  });
  
  // 2. 
  return result.sort((a, b) => {
    switch(activeSort.value) {
      case 'salaryHigh':
        const salAHigh = parseInt(a.salary.match(/\d+/g)[1] || a.salary.match(/\d+/g)[0]);
        const salBHigh = parseInt(b.salary.match(/\d+/g)[1] || b.salary.match(/\d+/g)[0]);
        return salBHigh - salAHigh;
      case 'salaryLow':
        const salALow = parseInt(a.salary.match(/\d+/g)[0]);
        const salBLow = parseInt(b.salary.match(/\d+/g)[0]);
        return salALow - salBLow;
      case 'distance':
        const distA = parseFloat(a.distance);
        const distB = parseFloat(b.distance);
        return distA - distB;
      case 'latest':
      default:
        return new Date(b.publishDate) - new Date(a.publishDate);
    }
  });
});

// 
const visibleModal = ref(false);
const currentJob = ref(null);
const showToast = ref(false);
const toastMessage = ref('');

// 
const openDetailModal = (job) => {
  currentJob.value = { ...job };
  visibleModal.value = true;
};

// 
const closeDetailModal = () => {
  visibleModal.value = false;
};

// 
const toggleCollect = () => {
  currentJob.value.collected = !currentJob.value.collected;
  toastMessage.value = currentJob.value.collected ? '' : '';
  showToast.value = true;
  setTimeout(() => {
    showToast.value = false;
  }, 2000);
};

// 
const applyJob = () => {
  toastMessage.value = '';
  showToast.value = true;
  setTimeout(() => {
    showToast.value = false;
    closeDetailModal();
  }, 2000);
};
</script>

