<template>
  <div class="satisfaction-chart-container">
    <div class="chart-header">
      <div class="chart-period-selector">
        <button 
          v-for="item in periodOptions" 
          :key="item.value"
          :class="{ active: currentPeriod === item.value }"
          @click="handlePeriodChange(item.value)"
        >
          {{ item.label }}
        </button>
      </div>
    </div>
    <div ref="chartRef" class="chart-content"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, watch, onUnmounted } from 'vue';
import * as echarts from 'echarts';

// 
const chartRef = ref(null);
const currentPeriod = ref('week');
const chartInstance = ref(null);

// 
const periodOptions = [
  { label: '', value: 'week' },
  { label: '', value: 'month' },
  { label: '', value: 'quarter' }
];

// 
const generateData = (type) => {
  let xData, baseScore = 4.2;
  
  switch(type) {
    case 'week':
      xData = ['', '', '', '', '', '', ''];
      break;
    case 'month':
      xData = Array.from({length: 30}, (_, i) => `${i+1}`);
      break;
    case 'quarter':
      xData = ['1', '2', '3', '4', '5', '6', '7', '8', '9'];
      break;
  }
  
  // 3.5-5.0
  const satisfactionData = xData.map((_, index) => {
    const randomFactor = Math.random() * 0.8 - 0.4; // -0.4  0.4 
    // 
    const trendFactor = type === 'quarter' ? index * 0.05 : 0;
    return Math.min(5.0, Math.max(3.5, baseScore + randomFactor + trendFactor)).toFixed(1);
  });
  
  // 
  const commentCountData = xData.map(() => Math.floor(Math.random() * 200 + 50));
  
  return { xData, satisfactionData, commentCountData };
};

// 
const initChart = () => {
  if (!chartRef.value) return;
  
  // 
  chartInstance.value = echarts.init(chartRef.value);
  
  // 
  updateChart();
  
  // 
  window.addEventListener('resize', handleResize);
};

// 
const updateChart = () => {
  const { xData, satisfactionData, commentCountData } = generateData(currentPeriod.value);
  
  const option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' },
      backgroundColor: 'rgba(255, 255, 255, 0.9)',
      borderColor: '#e6e6e6',
      textStyle: { color: '#333' },
      formatter: function(params) {
        let result = params[0].name + '<br/>';
        result += `: ${params[0].value} <br/>`;
        result += `: ${params[1].value} `;
        return result;
      }
    },
    legend: {
      data: ['', ''],
      top: 0,
      right: 0,
      textStyle: { color: '#666', fontSize: 12 }
    },
    grid: {
      left: '4%',
      right: '4%',
      bottom: '8%',
      top: '20%',
      containLabel: true
    },
    xAxis: {
      type: 'category',
      data: xData,
      axisLine: { lineStyle: { color: '#e6e6e6' } },
      axisLabel: { 
        color: '#666', 
        fontSize: 12,
        interval: currentPeriod.value === 'month' ? 4 : 0 // 
      }
    },
    yAxis: [
      {
        type: 'value',
        name: '',
        nameTextStyle: { color: '#666', fontSize: 12 },
        axisLine: { lineStyle: { color: '#e6e6e6' } },
        axisLabel: { 
          color: '#666', 
          fontSize: 12,
          formatter: '{value}'
        },
        splitLine: { lineStyle: { color: '#f5f5f5' } },
        min: 3,
        max: 5,
        splitNumber: 4
      },
      {
        type: 'value',
        name: '',
        nameTextStyle: { color: '#666', fontSize: 12 },
        axisLine: { lineStyle: { color: '#e6e6e6' } },
        axisLabel: { 
          color: '#666', 
          fontSize: 12
        },
        splitLine: { show: false }
      }
    ],
    series: [
      {
        name: '',
        type: 'line',
        data: satisfactionData,
        smooth: true,
        symbol: 'circle',
        symbolSize: 6,
        lineStyle: { width: 3, color: '#52c41a' },
        itemStyle: { 
          color: '#52c41a',
          borderColor: '#fff',
          borderWidth: 2
        },
        areaStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: 'rgba(82, 196, 26, 0.3)' },
            { offset: 1, color: 'rgba(82, 196, 26, 0.05)' }
          ])
        },
        markLine: {
          symbol: ['none', 'none'],
          data: [
            { yAxis: 4.5, name: '', lineStyle: { color: '#52c41a', type: 'dashed' } },
            { yAxis: 4.0, name: '', lineStyle: { color: '#faad14', type: 'dashed' } },
            { yAxis: 3.5, name: '', lineStyle: { color: '#f5222d', type: 'dashed' } }
          ],
          label: {
            fontSize: 10,
            color: '#666',
            formatter: '{b}'
          }
        },
        emphasis: {
          itemStyle: { symbolSize: 8 }
        }
      },
      {
        name: '',
        type: 'bar',
        yAxisIndex: 1,
        data: commentCountData,
        barWidth: '60%',
        itemStyle: {
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
            { offset: 0, color: '#1890ff' },
            { offset: 1, color: '#91d5ff' }
          ]),
          opacity: 0.6
        },
        emphasis: {
          itemStyle: {
            opacity: 0.8
          }
        }
      }
    ]
  };
  
  chartInstance.value.setOption(option);
};

// 
const handlePeriodChange = (value) => {
  currentPeriod.value = value;
};

// 
const handleResize = () => {
  if (chartInstance.value) {
    chartInstance.value.resize();
  }
};

// 
onMounted(() => {
  initChart();
});

watch(currentPeriod, () => {
  updateChart();
});

onUnmounted(() => {
  window.removeEventListener('resize', handleResize);
  if (chartInstance.value) {
    chartInstance.value.dispose();
  }
});
</script>

<style scoped>
.satisfaction-chart-container {
  position: relative;
  border-radius: 8px;
  padding: 10px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.chart-period-selector {
  top: 9px;
  position: absolute;
  right: 20px;
  display: flex;
  gap: 8px;
}

.chart-period-selector button {
  padding: 6px 16px;
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  background-color: #f9f9f9;
  color: #666;
  cursor: pointer;
  transition: all 0.2s;
  font-size: 14px;
}

.chart-period-selector button:hover {
  border-color: #1890ff;
  color: #1890ff;
}

.chart-period-selector button.active {
  background-color: #1890ff;
  color: white;
  border-color: #1890ff;
}

.chart-content {
  position: relative;
  top: 15px;
  flex: 1;
  width: 100%;
}
</style>