<template>
  <div class="merchant-ranking-page">
    <!--  -->
    <header class="page-header">
      <div class="container">
        <h1 class="page-title"></h1>
        <p class="page-description"></p>
      </div>
    </header>

    <!--  -->
    <section class="filter-section">
      <div class="container">
        <div class="filter-container">
          <div class="filter-row">
            <div class="filter-item">
              <label></label>
              <select v-model="filters.rankType" @change="filterMerchants">
                <option value="rating"></option>
                <option value="popularity"></option>
                <option value="service"></option>
                <option value="price"></option>
              </select>
            </div>
            
            <div class="filter-item">
              <label></label>
              <select v-model="filters.mall" @change="filterMerchants">
                <option value="all"></option>
                <option value="central"></option>
                <option value="east"></option>
                <option value="time"></option>
              </select>
            </div>
            
            <div class="filter-item">
              <label></label>
              <select v-model="filters.category" @change="filterMerchants">
                <option value="all"></option>
                <option value="electronics"></option>
                <option value="food"></option>
                <option value="clothing"></option>
                <option value="entertainment"></option>
              </select>
            </div>
            
            <div class="filter-item">
              <label></label>
              <select v-model="filters.timeRange" @change="filterMerchants">
                <option value="quarter"></option>
                <option value="month"></option>
                <option value="week"></option>
                <option value="year"></option>
              </select>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!--  -->
    <section class="stats-section">
      <div class="container">
        <h2 class="section-title"></h2>
        <div class="stats-grid">
          <div class="stat-card">
            <div class="stat-icon"></div>
            <div class="stat-info">
              <p class="stat-label"></p>
              <p class="stat-value">{{ stats.averageRating.toFixed(1) }}</p>
            </div>
          </div>
          
          <div class="stat-card">
            <div class="stat-icon"></div>
            <div class="stat-info">
              <p class="stat-label"></p>
              <p class="stat-value">{{ stats.totalMerchants }}</p>
            </div>
          </div>
          
          <div class="stat-card">
            <div class="stat-icon"></div>
            <div class="stat-info">
              <p class="stat-label"></p>
              <p class="stat-value">{{ stats.totalReviews }}</p>
            </div>
          </div>
          
          <div class="stat-card">
            <div class="stat-icon"></div>
            <div class="stat-info">
              <p class="stat-label"></p>
              <p class="stat-value">{{ stats.ratingIncrease }}%</p>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!--  -->
    <section class="ranking-section">
      <div class="container">
        <div class="ranking-header">
          <h2 class="section-title"></h2>
          <button class="refresh-btn" @click="refreshRanking">
             
          </button>
        </div>
        
        <!--  -->
        <div class="ranking-table">
          <!--  -->
          <div class="table-header">
            <div class="table-cell rank-cell"></div>
            <div class="table-cell info-cell"></div>
            <div class="table-cell rating-cell"></div>
            <div class="table-cell review-cell"></div>
            <div class="table-cell trend-cell"></div>
            <div class="table-cell action-cell"></div>
          </div>
          
          <!--  -->
          <div 
            class="table-row" 
            v-for="(merchant, index) in displayedMerchants" 
            :key="merchant.id"
            :class="{ 'hover': hoverRow === merchant.id }"
            @mouseenter="hoverRow = merchant.id"
            @mouseleave="hoverRow = null"
          >
            <div class="table-cell rank-cell">
              <div class="rank-badge" :class="getRankClass(index)">
                {{ index + 1 }}
              </div>
            </div>
            
            <div class="table-cell info-cell">
              <div class="merchant-info">
                <img :src="merchant.logo" alt="logo" class="merchant-logo">
                <div class="merchant-details">
                  <p class="merchant-name">{{ merchant.name }}</p>
                  <p class="merchant-meta">{{ merchant.categoryName }} | {{ merchant.mallName }}</p>
                </div>
              </div>
            </div>
            
            <div class="table-cell rating-cell">
              <div class="rating-display">
                <div class="stars">
                  <span v-for="star in 5" :key="star" :class="{ 'filled': star <= Math.floor(merchant.rating) }"></span>
                </div>
                <span class="rating-value">{{ merchant.rating.toFixed(1) }}</span>
              </div>
            </div>
            
            <div class="table-cell review-cell">
              {{ merchant.reviewCount }}
            </div>
            
            <div class="table-cell trend-cell">
              <div class="trend-indicator" :class="getTrendClass(merchant.trend)">
                <span>{{ getTrendSymbol(merchant.trend) }}</span>
                {{ Math.abs(merchant.trend) }}
              </div>
            </div>
            
            <div class="table-cell action-cell">
              <button class="detail-btn" @click="viewDetails(merchant.id)"></button>
            </div>
          </div>
          
          <!--  -->
          <div v-if="displayedMerchants.length === 0" class="empty-state">
            
          </div>
        </div>
        
        <!--  -->
        <div class="load-more">
          <button 
            v-if="hasMore"
            class="load-more-btn"
            @click="loadMore"
          >
            
          </button>
          <p v-else class="no-more"></p>
        </div>
      </div>
    </section>

  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted } from 'vue';

// 
const filters = reactive({
  rankType: 'rating',
  mall: 'all',
  category: 'all',
  timeRange: 'quarter'
});

// 
const pageSize = 5;
const currentPage = ref(1);

// 
const hoverRow = ref(null);
const allMerchants = ref([]);

// 
const stats = reactive({
  averageRating: 0,
  totalMerchants: 0,
  totalReviews: 0,
  ratingIncrease: 0
});

//  - 
const merchantData = [
  {
    id: 1,
    name: "",
    logo: "https://picsum.photos/seed/tech1/60/60",
    category: "electronics",
    categoryName: "",
    mall: "central",
    mallName: "",
    rating: 4.8,
    reviewCount: 1256,
    trend: 2
  },
  {
    id: 2,
    name: "",
    logo: "https://picsum.photos/seed/tech2/60/60",
    category: "electronics",
    categoryName: "",
    mall: "east",
    mallName: "",
    rating: 4.7,
    reviewCount: 987,
    trend: -1
  },
  {
    id: 3,
    name: "",
    logo: "https://picsum.photos/seed/tech3/60/60",
    category: "electronics",
    categoryName: "",
    mall: "time",
    mallName: "",
    rating: 4.6,
    reviewCount: 842,
    trend: 3
  },
  {
    id: 4,
    name: "",
    logo: "https://picsum.photos/seed/tech4/60/60",
    category: "electronics",
    categoryName: "",
    mall: "central",
    mallName: "",
    rating: 4.5,
    reviewCount: 753,
    trend: 0
  },
  {
    id: 5,
    name: "",
    logo: "https://picsum.photos/seed/tech5/60/60",
    category: "electronics",
    categoryName: "",
    mall: "east",
    mallName: "",
    rating: 4.4,
    reviewCount: 621,
    trend: -2
  },
  {
    id: 6,
    name: "",
    logo: "https://picsum.photos/seed/food1/60/60",
    category: "food",
    categoryName: "",
    mall: "central",
    mallName: "",
    rating: 4.9,
    reviewCount: 2345,
    trend: 1
  },
  {
    id: 7,
    name: "",
    logo: "https://picsum.photos/seed/food2/60/60",
    category: "food",
    categoryName: "",
    mall: "time",
    mallName: "",
    rating: 4.7,
    reviewCount: 1876,
    trend: 0
  },
  {
    id: 8,
    name: "",
    logo: "https://picsum.photos/seed/cloth1/60/60",
    category: "clothing",
    categoryName: "",
    mall: "east",
    mallName: "",
    rating: 4.6,
    reviewCount: 1567,
    trend: -1
  },
  {
    id: 9,
    name: "",
    logo: "https://picsum.photos/seed/ent1/60/60",
    category: "entertainment",
    categoryName: "",
    mall: "central",
    mallName: "",
    rating: 4.5,
    reviewCount: 1234,
    trend: 2
  },
  {
    id: 10,
    name: "",
    logo: "https://picsum.photos/seed/ent2/60/60",
    category: "entertainment",
    categoryName: "",
    mall: "time",
    mallName: "",
    rating: 4.3,
    reviewCount: 987,
    trend: -3
  }
];

//  - 
const filteredMerchants = computed(() => {
  let result = [...allMerchants.value];
  
  // 
  if (filters.mall !== 'all') {
    result = result.filter(m => m.mall === filters.mall);
  }
  
  // 
  if (filters.category !== 'all') {
    result = result.filter(m => m.category === filters.category);
  }
  
  // 
  switch(filters.rankType) {
    case 'rating':
      result.sort((a, b) => b.rating - a.rating);
      break;
    case 'popularity':
      result.sort((a, b) => b.reviewCount - a.reviewCount);
      break;
    case 'service':
      result.sort((a, b) => (b.rating * 0.7 + b.reviewCount * 0.3) - (a.rating * 0.7 + a.reviewCount * 0.3));
      break;
    case 'price':
      result.sort((a, b) => b.rating - a.rating); // 
      break;
  }
  
  return result;
});

//  - 
const displayedMerchants = computed(() => {
  const start = (currentPage.value - 1) * pageSize;
  const end = start + pageSize;
  return filteredMerchants.value.slice(start, end);
});

//  - 
const hasMore = computed(() => {
  return currentPage.value * pageSize < filteredMerchants.value.length;
});

// 
onMounted(() => {
  // API
  setTimeout(() => {
    allMerchants.value = merchantData;
    calculateStats();
  }, 300);
});

// 
function calculateStats() {
  if (allMerchants.value.length === 0) return;
  
  const totalRating = allMerchants.value.reduce((sum, m) => sum + m.rating, 0);
  stats.averageRating = totalRating / allMerchants.value.length;
  stats.totalMerchants = allMerchants.value.length;
  stats.totalReviews = allMerchants.value.reduce((sum, m) => sum + m.reviewCount, 0);
  stats.ratingIncrease = 8.3; // 
}

// 
function filterMerchants() {
  currentPage.value = 1; // 
}

// 
function loadMore() {
  currentPage.value++;
}

// 
function refreshRanking() {
  // 
  allMerchants.value = [];
  
  setTimeout(() => {
    allMerchants.value = merchantData;
    calculateStats();
    currentPage.value = 1;
  }, 500);
}

// 
function viewDetails(id) {
  console.log(`ID: ${id} `);
  // 
  // router.push(`/merchant/${id}`);
}

// 
function getRankClass(index) {
  if (index === 0) return 'rank-1';
  if (index === 1) return 'rank-2';
  if (index === 2) return 'rank-3';
  return 'rank-other';
}

// 
function getTrendClass(trend) {
  if (trend > 0) return 'trend-up';
  if (trend < 0) return 'trend-down';
  return 'trend-stable';
}

// 
function getTrendSymbol(trend) {
  if (trend > 0) return '';
  if (trend < 0) return '';
  return '';
}
</script>

<style scoped>
