  border-bottom: 1px solid #e9ecef;
  padding-bottom: 10px;
}

.plan-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.plan-card {
  background: #f8f9fa;
  border-radius: 10px;
  overflow: hidden;
  border: 1px solid #e9ecef;
  transition: all 0.3s;
  position: relative;
  box-shadow: 0 2px 5px rgba(0,0,0,0.05);
}

.plan-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
  border-color: #dee2e6;
}

.plan-card.featured {
  transform: scale(1.05);
  border-color: #ffcc80;
  background-color: #fff8e1;
}

.featured-badge {
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background: #ffb74d;
  color: #e65100;
  padding: 3px 20px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: bold;
  box-shadow: 0 2px 10px rgba(255, 183, 77, 0.3);
}

.plan-header {
  padding: 30px 20px;
  border-bottom: 1px solid #e9ecef;
  text-align: center;
}
.phhj{
  position: relative;
  top: 30px;
}

.plan-header h4 {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 10px;
  margin-top: 0;
  color: #2c3e50;
}

.plan-price {
  font-size: 32px;
  font-weight: 800;
  color: #1976d2;
}

.plan-price span {
  font-size: 16px;
  font-weight: normal;
  color: #6c757d;
}

.plan-features {
  padding: 25px 20px;
}

.plan-features ul {
  list-style: none;
  padding: 0;
  margin: 0 0 30px 0;
}

.plan-features li {
  padding: 10px 0;
  border-bottom: 1px solid #e9ecef;
  display: flex;
  align-items: center;
  color: #495057;
}

.plan-features li:before {
  content: "";
  color: #43a047;
  margin-right: 10px;
}

.subscribe-btn {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  font-weight: 600;
}

.plan-card.featured .subscribe-btn {
  background: linear-gradient(45deg, #ffb74d, #ffa726);
  color: #e65100;
  box-shadow: 0 4px 15px rgba(255, 183, 77, 0.3);
}

.plan-card.featured .subscribe-btn:hover {
  background: linear-gradient(45deg, #ffa726, #ff9800);
  box-shadow: 0 6px 20px rgba(255, 183, 77, 0.5);
}

