<template>
  <div class="home-carousel">
    <!--  -->
    <div 
      class="carousel-slides" 
      :style="{ transform: `translateX(-${currentIndex * 100}%)` }"
      @mouseenter="stopAutoPlay"
      @mouseleave="startAutoPlay"
      ref="slidesContainer" 
    >
      <!--  -->
      <slot></slot>
    </div>

    <!--  -->
    <div class="carousel-controls">
      <button 
        v-for="(item, index) in slidesCount" 
        :key="index" 
        class="control-dot"
        :class="{ active: currentIndex === index }"
        @click="goToSlide(index)"
      ></button>
    </div>

    <!--  -->
    <button class="carousel-btn prev-btn" @click="prevSlide">
      <i class="btn-icon"></i>
    </button>
    <button class="carousel-btn next-btn" @click="nextSlide">
      <i class="btn-icon"></i>
    </button>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted, defineProps, watch } from 'vue';

// 
const props = defineProps({
  slidesCount: {
    type: Number,
    required: true
  }
});

// 
const currentIndex = ref(0);
let autoPlayTimer = null;
const slidesContainer = ref(null);  // DOM

// 
const startAutoPlay = () => {
  // 
};

const stopAutoPlay = () => {
  if (autoPlayTimer) {
    clearInterval(autoPlayTimer);
    autoPlayTimer = null;
  }
};

// 
const resetCurrentSlide = () => {
  // DOM
  const slides = slidesContainer.value?.children || [];
  const currentSlide = slides[currentIndex.value];
  
  if (currentSlide) {
    // 1. 
    currentSlide.scrollTop = 0;
    
    // 2. 
    const scrollableElements = currentSlide.querySelectorAll(
      'div, section, article, iframe'  // 
    );
    scrollableElements.forEach(el => {
      if (el.scrollHeight > el.clientHeight) {  // 
        el.scrollTop = 0;
      }
    });

    // 3. 
    const slideComponent = currentSlide.__vueComponent__;
    if (slideComponent?.reset) {
      slideComponent.reset();  // reset
    }
  }
};

// 
const prevSlide = () => {
  currentIndex.value = (currentIndex.value - 1 + props.slidesCount) % props.slidesCount;
};

const nextSlide = () => {
  currentIndex.value = (currentIndex.value + 1) % props.slidesCount;
};

const goToSlide = (index) => {
  currentIndex.value = index;
};

// 
watch(currentIndex, () => {
  // 
  setTimeout(resetCurrentSlide, 300);  // 
});

// 
onMounted(() => {
  // 
  setTimeout(resetCurrentSlide, 500);
});

onUnmounted(() => {
  stopAutoPlay();
});

// 
defineExpose({ currentIndex });
</script>
<style scoped>
.home-carousel {
  position: relative;
  width: 100%;
  height: calc(100vh - 90px); 
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.carousel-slides {
  display: flex;
  height: 100%;
  transition: transform 1s cubic-bezier(0.4, 0, 0.2, 1);
}

/*  */
.carousel-controls {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 15px;
  z-index: 10;
}

.control-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: rgba(135,206,235);
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.control-dot.active {
  background-color: blue;
  /*  */
  box-shadow: 0 0 0 4px rgba(22, 93, 255, 0.2);
}

.control-dot:hover:not(.active) {
  background-color: rgba(255, 255, 255, 0.8);
  /*  */
}

/*  */
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: orange;
  backdrop-filter: blur(8px);
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
}

.home-carousel:hover .carousel-btn {
  opacity: 1;
}

.prev-btn {
  left: 30px;
}

.next-btn {
  right: 30px;
}

.carousel-btn:hover {
  background-color: orange;
  /*  */
  transform: translateY(-50%);
}

.btn-icon {
  font-weight: bold;
}

/*  */
@media (max-width: 768px) {
  .home-carousel {
    height: calc(80vh - 90px);
  }
  
  .carousel-controls {
    bottom: 20px;
    gap: 10px;
  }
  
  .control-dot {
    width: 12px;
    height: 12px;
  }
  
  .carousel-btn {
    width: 45px;
    height: 45px;
    font-size: 1.2rem;
  }
  
  .prev-btn {
    left: 2px;
  }
  
  .next-btn {
    right: 2px;
  }
}
</style>