<template>
  <div class="business-type-chart-container">
    <div ref="chartRef" class="chart-content"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';
import * as echarts from 'echarts';

// 
const chartRef = ref(null);
const chartInstance = ref(null);

// 
const businessData = [
  { name: '', value: 35 },
  { name: '', value: 25 },
  { name: '', value: 18 },
  { name: '', value: 12 },
  { name: '', value: 8 },
  { name: '', value: 2 }
];

// 
const initChart = () => {
  if (!chartRef.value) return;
  
  // 
  chartInstance.value = echarts.init(chartRef.value);
  
  // 
  const option = {
    tooltip: {
      trigger: 'item',
      formatter: '{b}: {c}% ({d}%)',
      backgroundColor: 'rgba(255, 255, 255, 0.9)',
      borderColor: '#e6e6e6',
      textStyle: { color: '#333' }
    },
    legend: {
      orient: 'vertical',
      right: '1%',
      top: 'center',
      textStyle: { color: '#666', fontSize: 12 }
    },
    series: [
      {
        name: '',
        type: 'pie',
        radius: ['40%', '70%'],
        center: ['35%', '50%'],
        avoidLabelOverlap: false,
        itemStyle: {
          borderRadius: 8,
          borderColor: '#fff',
          borderWidth: 2
        },
        label: {
          show: true,
          position: 'outside',
          fontSize: 12,
          color: '#666',
          formatter: '{b}: {c}%'
        },
        emphasis: {
          label: {
            show: true,
            fontSize: 14,
            fontWeight: 'bold'
          }
        },
        labelLine: {
          show: true,
          length: 20,
          length2: 10,
          lineStyle: {
            color: '#999'
          }
        },
        data: businessData,
        color: [
          '#5b8ff9', // 
          '#61ddaa', // 
          '#65789b', // 
          '#f68484', // 
          '#ffb84d', // 
          '#9287e7'  // 
        ]
      }
    ]
  };
  
  chartInstance.value.setOption(option);
};

// 
const handleResize = () => {
  if (chartInstance.value) {
    chartInstance.value.resize();
  }
};

// 
onMounted(() => {
  initChart();
  window.addEventListener('resize', handleResize);
});

onUnmounted(() => {
  window.removeEventListener('resize', handleResize);
  if (chartInstance.value) {
    chartInstance.value.dispose();
  }
});
</script>

<style scoped>
.business-type-chart-container {
  position: relative;
  border-radius: 8px;
  padding: 10px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.chart-content {
  flex: 1;
  width: 100%;
}
</style>