<script setup>
import { ref, watch, onMounted } from 'vue';

const messages = ref([]);
const inputMessage = ref('');
const isLoading = ref(false);
const chatContainer = ref(null);

// 
const presetAnswers = {
  '': ' 10:00 - 22:0010:00 - 18:00',
  '': '3\n- \n- \n- \n- ',
  '': '22001',
  '': '4\n1F\n2F\n3F\n4FKTV',
  '': 'APP1',
  '': '\n- 1000100\n- 50\n- 8\n- 15',
  '': '\n- \n- 19.5\n- 59\n- 108.5',
  '': '\n- 400-888-8888\n- 1\n- \n24'
};

//  - AI
const systemPrompt = `AI""




- 
-  10:00 - 22:00
- 1F-2F-3F-4F-
- WiFiVIP`;

// 
const formatMessage = (content) => {
  return content.replace(/\n/g, '<br>');
};

// 
const checkPresetQuestion = (question) => {
  // 
  if (presetAnswers[question]) {
    return presetAnswers[question];
  }
  
  // 
  for (const [key, answer] of Object.entries(presetAnswers)) {
    if (question.includes(key) || key.includes(question)) {
      return answer;
    }
  }
  
  return null;
};

// AI
const getAIResponse = async (userMessage) => {
  // 
  const presetAnswer = checkPresetQuestion(userMessage);
  if (presetAnswer) {
    return presetAnswer;
  }

  // 
  const conversation = [
    { role: 'system', content: systemPrompt },
    ...messages.value.map(msg => ({
      role: msg.sender === 'ai' ? 'assistant' : 'user',
      content: msg.content
    })),
    { role: 'user', content: userMessage }
  ];

  // API
  const apiEndpoints = [
    // Llama API ()
    {
      url: 'https://api-free.llama-api.com/chat/completions',
      options: {
        model: 'llama-3-8b-chat',
        messages: conversation,
        temperature: 0.7,
        max_tokens: 1000
      }
    },
    // ChatGPT 
    {
      url: 'https://chatgpt-api.shn.hk/v1/',
      options: {
        model: 'gpt-3.5-turbo',
        messages: conversation,
        temperature: 0.7
      }
    },
    // Another free API
    {
      url: 'https://free.churchless.tech/v1/chat/completions',
      options: {
        model: 'gpt-3.5-turbo',
        messages: conversation,
        temperature: 0.7
      }
    }
  ];

  // API
  for (const endpoint of apiEndpoints) {
    try {
      const response = await fetch(endpoint.url, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(endpoint.options)
      });
      
      if (response.ok) {
        const data = await response.json();
        return data.choices[0].message.content;
      }
    } catch (error) {
      console.log(`API ${endpoint.url} failed:`, error.message);
      continue;
    }
  }

  // API
  return generateSmartResponse(userMessage, conversation);
};

// 
const generateSmartResponse = (message, conversation) => {
  const lowerMsg = message.toLowerCase();
  
  // 
  const knowledgeBase = {
    // 
    '|||': () => {
      const now = new Date();
      const hour = now.getHours();
      let weather = '';
      if (hour > 18 || hour < 6) weather = '';
      if (Math.random() > 0.7) weather = '';
      return `(${hour})${weather}${20 + Math.floor(Math.random() * 10)}`;
    },
    
    '|||hello': () => [
      '',
      '',
      ''
    ][Math.floor(Math.random() * 3)],
    
    '||': () => [
      '',
      '',
      ''
    ][Math.floor(Math.random() * 3)],
    
    '|': () => '',
    
    '|': () => 'AI',
    
    '||': () => 'AI',
    
  
    
    // 
    '||': () => {
      const now = new Date();
      return `${now.getHours()}:${now.getMinutes().toString().padStart(2, '0')}`;
    },
    
    '|': () => {
      const now = new Date();
      return `${now.getFullYear()}${now.getMonth() + 1}${now.getDate()}`;
    },
    
    // 
    '||': () => {
      if (message.includes('')) return '';
      if (message.includes('')) return '';
      return '...';
    },
    
    '|': () => {
      if (message.includes('')) return '';
      if (message.includes('')) return '';
      if (message.includes('')) return '';
      return '...';
    }
  };
  
  // 
  for (const [pattern, handler] of Object.entries(knowledgeBase)) {
    const regex = typeof pattern === 'string' ? new RegExp(pattern) : pattern;
    const match = message.match(regex);
    
    if (match) {
      return typeof handler === 'function' ? handler(match) : handler;
    }
  }
  
  // 
  const topics = [
    '',
    '',
    '',
    ''
  ];
  
  const contents = [
    `${message}`,
    `${message}`,
    `${message}`
  ];
  
  return `${topics[Math.floor(Math.random() * topics.length)]} ${contents[Math.floor(Math.random() * contents.length)]} `;
};

// 
const sendMessage = async () => {
  if (!inputMessage.value.trim() || isLoading.value) return;
  
  // 
  const userMessage = inputMessage.value.trim();
  messages.value.push({
    sender: 'user',
    content: userMessage
  });
  
  inputMessage.value = '';
  isLoading.value = true;
  
  // AI
  const loadingIndex = messages.value.length;
  messages.value.push({
    sender: 'ai',
    content: ''
  });
  
  try {
    // AI
    const aiResponse = await getAIResponse(userMessage);
    
    // AI
    messages.value[loadingIndex] = {
      sender: 'ai',
      content: aiResponse
    };
    
    // 
    nextTick(() => {
      if (chatContainer.value) {
        chatContainer.value.scrollTop = chatContainer.value.scrollHeight;
      }
    });
  } catch (error) {
    console.error('AI:', error);
    messages.value[loadingIndex] = {
      sender: 'ai',
      content: generateSmartResponse(userMessage)
    };
  } finally {
    isLoading.value = false;
  }
};

// 
const sendSuggestion = (suggestion) => {
  inputMessage.value = suggestion;
  sendMessage();
};

// 
const contactHuman = () => {
  messages.value.push({
    sender: 'ai',
    content: '...'
  });
  
  setTimeout(() => {
    messages.value.push({
      sender: 'staff',
      content: ''
    });
    
    // 
    nextTick(() => {
      if (chatContainer.value) {
        chatContainer.value.scrollTop = chatContainer.value.scrollHeight;
      }
    });
  }, 1500);
};

// 
watch(messages, () => {
  nextTick(() => {
    if (chatContainer.value) {
      chatContainer.value.scrollTop = chatContainer.value.scrollHeight;
    }
  });
}, { deep: true });

// nextTick
const nextTick = (fn) => {
  Promise.resolve().then(fn);
};

// 
onMounted(() => {
  console.log('AI');
  
  // 
  nextTick(() => {
    if (chatContainer.value) {
      chatContainer.value.scrollTop = chatContainer.value.scrollHeight;
    }
  });
});
</script>

