<template>
  <div class="hot-topics-radar-container">
    <div ref="chartRef" class="chart-content"></div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue';
import * as echarts from 'echarts';

// 
const chartRef = ref(null);
const chartInstance = ref(null);

// 
const topicsData = {
  dimensions: [
    '', 
    '', 
    '', 
    '', 
    '', 
    '', 
    ''
  ],
  currentWeek: [85, 72, 90, 68, 60, 88, 75],
  lastWeek: [70, 65, 78, 72, 55, 75, 68]
};

// 
const initChart = () => {
  if (!chartRef.value) return;
  
  // 
  chartInstance.value = echarts.init(chartRef.value);
  
  // 
  const option = {
    tooltip: {
      trigger: 'item',
      backgroundColor: 'rgba(255, 255, 255, 0.9)',
      borderColor: '#e6e6e6',
      textStyle: { color: '#333' },
      formatter: function(params) {
        return `${params.seriesName}<br/>${params.name}: ${params.value}`;
      }
    },
    legend: {
      orient: 'horizontal',
      bottom: '-2%',
      right: '2%',
      textStyle: { color: '#666', fontSize: 12 },
      data: ['', '']
    },
    radar: {
      indicator: topicsData.dimensions.map(name => ({
        name,
        max: 100,
        min: 0
      })),
      shape: 'polygon',
      splitNumber: 4,
      name: {
        textStyle: {
          color: '#666',
          fontSize: 11
        }
      },
      splitLine: {
        lineStyle: {
          color: '#e6e6e6',
          type: 'dashed'
        }
      },
      splitArea: {
        areaStyle: {
          color: ['rgba(255,255,255,0.1)', 'rgba(240,240,240,0.1)']
        }
      },
      axisLine: {
        lineStyle: {
          color: '#ccc'
        }
      }
    },
    series: [
      {
        name: '',
        type: 'radar',
        data: [
          {
            value: topicsData.currentWeek,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(24, 144, 255, 0.4)' },
                { offset: 1, color: 'rgba(24, 144, 255, 0.1)' }
              ])
            },
            lineStyle: {
              color: '#1890ff',
              width: 2
            },
            itemStyle: {
              color: '#1890ff',
              borderColor: '#fff',
              borderWidth: 2
            },
            symbol: 'circle',
            symbolSize: 6
          }
        ]
      },
      {
        name: '',
        type: 'radar',
        data: [
          {
            value: topicsData.lastWeek,
            areaStyle: {
              color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                { offset: 0, color: 'rgba(245, 158, 11, 0.4)' },
                { offset: 1, color: 'rgba(245, 158, 11, 0.1)' }
              ])
            },
            lineStyle: {
              color: '#f59e0b',
              width: 2,
              type: 'dashed'
            },
            itemStyle: {
              color: '#f59e0b',
              borderColor: '#fff',
              borderWidth: 2
            },
            symbol: 'circle',
            symbolSize: 6
          }
        ]
      }
    ]
  };
  
  chartInstance.value.setOption(option);
};

// 
const handleResize = () => {
  if (chartInstance.value) {
    chartInstance.value.resize();
  }
};

// 
onMounted(() => {
  initChart();
  window.addEventListener('resize', handleResize);
});

onUnmounted(() => {
  window.removeEventListener('resize', handleResize);
  if (chartInstance.value) {
    chartInstance.value.dispose();
  }
});
</script>

<style scoped>
.hot-topics-radar-container {
  position: relative;
  border-radius: 8px;
  padding: 10px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.chart-content {
  flex: 1;
  width: 100%;
}
</style>