<template>
  <div class="slide slide-core-value">
    <div class="slide-bg">
      <div class="slide-overlay"></div>
      <!--  -->
      <div class="tech-decoration left"></div>
      <div class="tech-decoration right"></div>
      
      <div class="slide-content">
        <!--  -->
        <div class="notification-container">
          <!--  -->
          <div class="recommended-banner">
            <!--  -->
            <div class="banner-tech-decoration left"></div>
            <div class="banner-tech-decoration right"></div>
            
            <div class="banner-slides" :style="{ transform: `translateX(-${currentBannerIndex * 100}%)` }">
              <div class="banner-slide" v-for="(banner, index) in banners" :key="index">
                <div class="banner-content">
                  <div class="banner-header">
                    <span class="banner-tag">{{ banner.tag }}</span>
                    <h2 class="banner-title">{{ banner.title }}</h2>
                  </div>
                  <p class="banner-desc">{{ banner.description }}</p>
                  <button class="banner-btn" @click="openBannerDetail(banner)">
                    {{ banner.buttonText }}
                  </button>
                </div>
                <div class="banner-icon">
                  <!-- emoji -->
                  <span v-if="banner.icon === 'shopping-bag'"></span>
                  <span v-if="banner.icon === 'member-card'"></span>
                  <span v-if="banner.icon === 'new-store'"></span>
                </div>
                <button class="banner-next" @click="nextBanner">
                  <i class="arrow-right"></i>
                </button>
              </div>
            </div>
            <!--  -->
            <div class="banner-dots">
              <span 
                v-for="(dot, index) in banners.length" 
                :key="index"
                :class="{ active: currentBannerIndex === index }"
                @click="goToBanner(index)"
              ></span>
            </div>
          </div>

          <!--  -->
          <div class="notification-section">
            <div class="notification-header">
              <h2 class="section-title"></h2>
              <div class="filter-tabs">
                <button 
                  v-for="tab in tabs" 
                  :key="tab.value"
                  :class="{ active: activeTab === tab.value }"
                  @click="switchTab(tab.value)"
                >
                  {{ tab.label }}
                </button>
              </div>
            </div>

            <div class="notification-list">
              <div 
                class="notification-item" 
                v-for="(item, index) in filteredNotifications" 
                :key="item.id"
                :class="{ unread: !item.read }"
                @click="openNotificationDetail(item)"
              >
                <div class="notification-text">
                  <h3 class="notification-title">{{ item.title }}</h3>
                  <p class="notification-desc">{{ item.description }}</p>
                </div>
                <div class="notification-date">
                  {{ formatDate(item.date) }}
                  <span class="unread-dot" v-if="!item.read"></span>
                </div>
              </div>
            </div>

            <div class="view-more">
              <a href="#" @click.prevent="loadMore"></a>
            </div>
          </div>
        </div>

        <!--  -->
        <div class="three-containers">
          <!--  -->
          <div class="info-container event-container">
            <div class="container-header">
              <span class="icon"></span>
              <h3></h3>
            </div>
            <div class="container-content">
              <div class="event-item" v-for="(event, index) in mallEvents" :key="index" @click="openEventDetail(event)">
                <div class="event-status" :class="event.status">{{ event.statusText }}</div>
                <h4 class="event-title">{{ event.title }}</h4>
                <div class="event-date"> {{ event.date }}</div>
                <div class="event-location"> {{ event.location }}</div>
                <p class="event-desc">{{ event.description }}</p>
                <a href="#" class="view-details" @click.prevent="openEventDetail(event)"></a>
              </div>
              <a href="#" class="view-more-link" @click.prevent="viewMoreEvents"></a>
            </div>
          </div>

          <!--  -->
          <div class="info-container public-service-container">
            <div class="container-header">
              <span class="icon"></span>
              <h3></h3>
            </div>
            <div class="container-content">
              <div class="service-title"></div>
              <div class="service-item" v-for="(service, index) in publicServices" :key="index" @click="openServiceDetail(service)">
                <h4 class="service-name">{{ service.name }}</h4>
                <p class="service-desc">{{ service.description }}</p>
                <div class="service-meta">
                  <span class="service-date"> {{ service.date }}</span>
                  <span class="service-location" v-if="service.location"> {{ service.location }}</span>
                </div>
              </div>
              
              <div class="stall-section">
                <div class="service-title"></div>
                <p class="stall-desc">{{ stallRecruitment.description }}</p>
                <div class="stall-benefits">
                  <div class="benefit-item" v-for="(benefit, index) in stallRecruitment.benefits" :key="index">
                    <span class="benefit-check"></span> {{ benefit }}
                  </div>
                </div>
                <button class="apply-btn" @click="openStallApplication"></button>
              </div>
            </div>
          </div>

          <!--  -->
          <div class="info-container part-time-container">
            <div class="container-header">
              <span class="icon"></span>
              <h3></h3>
            </div>
            <div class="container-content">
              <div class="job-item" v-for="(job, index) in partTimeJobs" :key="index" @click="openJobDetail(job)">
                <div class="job-header">
                  <h4 class="job-title">{{ job.title }}</h4>
                  <span class="job-salary">{{ job.salary }}</span>
                </div>
                <div class="job-meta">
                  <span class="job-tag" v-for="(tag, tIndex) in job.tags" :key="tIndex">{{ tag }}</span>
                </div>
                <p class="job-desc">{{ job.description }}</p>
                <a href="#" class="view-details" @click.prevent="openJobDetail(job)"></a>
              </div>
              <a href="#" class="view-more-link" @click.prevent="viewMoreJobs"></a>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="slide-modals">
      <!--  -->
      <div class="modal-overlay" v-if="showBannerModal" @click="closeBannerModal">
        <div class="modal-content banner-modal" @click.stop>
          <div class="modal-header">
            <h2>{{ currentBanner.title }}</h2>
            <button class="modal-close" @click="closeBannerModal"></button>
          </div>
          <div class="modal-body">
            <div class="banner-detail">
              <div class="banner-icon-large">
                <span v-if="currentBanner.icon === 'shopping-bag'"></span>
                <span v-if="currentBanner.icon === 'member-card'"></span>
                <span v-if="currentBanner.icon === 'new-store'"></span>
              </div>
              <div class="banner-tag-large">{{ currentBanner.tag }}</div>
              <p class="banner-detail-desc">{{ currentBanner.description }}</p>
              <div class="banner-detail-info">
                <div class="info-item">
                  <span class="info-label"></span>
                  <span class="info-value">{{ currentBanner.date || '' }}</span>
                </div>
                <div class="info-item">
                  <span class="info-label"></span>
                  <span class="info-value">{{ currentBanner.participation || '' }}</span>
                </div>
              </div>
              <button class="modal-action-btn"></button>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="modal-overlay" v-if="showNotificationModal" @click="closeNotificationModal">
        <div class="modal-content notification-modal" @click.stop>
          <div class="modal-header">
            <h2>{{ currentNotification.title }}</h2>
            <button class="modal-close" @click="closeNotificationModal"></button>
          </div>
          <div class="modal-body">
            <div class="notification-detail">
              <div class="detail-meta">
                <span class="detail-date">{{ formatDate(currentNotification.date) }}</span>
                <span class="detail-type" :class="currentNotification.type">{{ currentNotification.type === 'notice' ? '' : '' }}</span>
              </div>
              <div class="detail-content">
                <p>{{ currentNotification.description }}</p>
                <div v-if="currentNotification.additionalInfo" class="additional-info">
                  <h4></h4>
                  <p>{{ currentNotification.additionalInfo }}</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="modal-overlay" v-if="showEventModal" @click="closeEventModal">
        <div class="modal-content event-modal" @click.stop>
          <div class="modal-header">
            <h2>{{ currentEvent.title }}</h2>
            <button class="modal-close" @click="closeEventModal"></button>
          </div>
          <div class="modal-body">
            <div class="event-detail">
              <div class="event-status-badge" :class="currentEvent.status">{{ currentEvent.statusText }}</div>
              <div class="event-gallery">
                <img v-for="(img, index) in currentEvent.images || ['https://picsum.photos/id/'+(20+index)+'/800/400']" :key="index" :src="img" alt="">
              </div>
              <div class="event-info">
                <div class="info-grid">
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentEvent.date }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentEvent.location }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentEvent.participation || '' }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentEvent.contact || '021-12345678' }}</span>
                  </div>
                </div>
              </div>
              <div class="event-description">
                <h4></h4>
                <p>{{ currentEvent.description }}</p>
                <p v-if="currentEvent.details">{{ currentEvent.details }}</p>
              </div>
              <button class="modal-action-btn"></button>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="modal-overlay" v-if="showStallModal" @click="closeStallModal">
        <div class="modal-content stall-modal" @click.stop>
          <div class="modal-header">
            <h2></h2>
            <button class="modal-close" @click="closeStallModal"></button>
          </div>
          <div class="modal-body">
            <form @submit.prevent="submitStallApplication">
              <div class="form-row">
                <div class="form-group">
                  <label></label>
                  <input type="text" v-model="stallForm.name" required placeholder="">
                </div>
                <div class="form-group">
                  <label></label>
                  <input type="tel" v-model="stallForm.phone" required placeholder="">
                </div>
              </div>
              <div class="form-group">
                <label></label>
                <select v-model="stallForm.type" required>
                  <option value=""></option>
                  <option value="handcraft"></option>
                  <option value="food"></option>
                  <option value="creative"></option>
                  <option value="other"></option>
                </select>
              </div>
              <div class="form-group">
                <label></label>
                <input type="text" v-model="stallForm.stallName" required placeholder="">
              </div>
              <div class="form-group">
                <label></label>
                <textarea v-model="stallForm.description" required rows="4" placeholder=""></textarea>
              </div>
              <div class="form-group">
                <label></label>
                <select v-model="stallForm.duration" required>
                  <option value=""></option>
                  <option value="weekend"></option>
                  <option value="month"></option>
                  <option value="quarter"></option>
                  <option value="longterm"></option>
                </select>
              </div>
              <div class="form-group">
                <label>/</label>
                <div class="upload-area" @click="triggerFileInput">
                  <input type="file" ref="fileInput" style="display: none;" accept="image/*" @change="handleFileUpload">
                  <span v-if="!stallForm.image"></span>
                  <span v-else>: {{ stallForm.image.name }}</span>
                </div>
              </div>
              <button type="submit" class="submit-btn"></button>
            </form>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="modal-overlay" v-if="showJobModal" @click="closeJobModal">
        <div class="modal-content job-modal" @click.stop>
          <div class="modal-header">
            <h2>{{ currentJob.title }}</h2>
            <button class="modal-close" @click="closeJobModal"></button>
          </div>
          <div class="modal-body">
            <div class="job-detail">
              <div class="job-salary-large">{{ currentJob.salary }}</div>
              <div class="job-tags">
                <span class="job-tag" v-for="(tag, index) in currentJob.tags" :key="index">{{ tag }}</span>
              </div>
              <div class="job-description">
                <h4></h4>
                <p>{{ currentJob.description }}</p>
                <div v-if="currentJob.requirements">
                  <h4></h4>
                  <ul>
                    <li v-for="(req, index) in currentJob.requirements" :key="index">{{ req }}</li>
                  </ul>
                </div>
              </div>
              <div class="job-info">
                <div class="info-grid">
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentJob.location || '' }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentJob.time || '' }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentJob.benefits || '+' }}</span>
                  </div>
                  <div class="info-item">
                    <span class="info-label"></span>
                    <span class="info-value">{{ currentJob.contact || 'hr@mall.com' }}</span>
                  </div>
                </div>
              </div>
              <button class="modal-action-btn" @click="openJobApplication"></button>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <div class="modal-overlay" v-if="showSuccessModal" @click="closeSuccessModal">
        <div class="modal-content success-modal" @click.stop>
          <div class="success-icon">
            <svg viewBox="0 0 24 24" width="60" height="60">
              <path d="M9 16.17L4.83 12l-1.42 1.41L9 19 21 7l-1.41-1.41z" fill="#4CAF50" />
            </svg>
          </div>
          <h2>{{ successMessage.title }}</h2>
          <p>{{ successMessage.content }}</p>
          <button class="confirm-btn" @click="closeSuccessModal"> </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUnmounted } from 'vue';

// 
const banners = ref([
  {
    tag: '2025""',
    title: '',
    description: '5.5',
    buttonText: '',
    icon: 'shopping-bag',
    date: '202555-515',
    participation: ''
  },
  {
    tag: '',
    title: '',
    description: '',
    buttonText: '',
    icon: 'member-card',
    date: '202561-831',
    participation: ''
  },
  {
    tag: '',
    title: '',
    description: '1000300',
    buttonText: '',
    icon: 'new-store',
    date: '2025618',
    participation: ''
  }
]);

// 
const currentBannerIndex = ref(0);
let bannerTimer = null;

// 
const tabs = [
  { label: '', value: 'all' },
  { label: '', value: 'notice' },
  { label: '', value: 'activity' }
];

// 
const notifications = ref([
  {
    id: 1,
    title: '',
    description: '7252:00-5:00',
    date: '2023-07-20',
    type: 'notice',
    read: false,
    additionalInfo: ''
  },
  {
    id: 2,
    title: '[] DIY',
    description: 'DIY722-23 14:00-17:003',
    date: '2023-07-18',
    type: 'activity',
    read: false,
    additionalInfo: '20'
  },
  {
    id: 3,
    title: '',
    description: '72118:00',
    date: '2023-07-15',
    type: 'notice',
    read: true
  }
]);

// 
const mallEvents = ref([
  {
    title: '',
    date: '615 - 620',
    location: '1',
    description: '',
    details: '202327',
    status: 'ongoing',
    statusText: '',
    participation: '',
    contact: '021-88888888',
    images: ['https://picsum.photos/id/21/800/400', 'https://picsum.photos/id/22/800/400']
  },
  {
    title: '',
    date: '625 - 630',
    location: '3',
    description: '',
    details: '50109100',
    status: 'upcoming',
    statusText: '',
    participation: '',
    contact: '021-99999999'
  },
  {
    title: '',
    date: '75 - 710',
    location: '2',
    description: '',
    details: 'DIY3-12',
    status: 'upcoming',
    statusText: '',
    participation: '',
    contact: '021-77777777'
  }
]);

// 
const publicServices = ref([
  {
    name: '',
    description: '',
    date: '620',
    location: ''
  },
  {
    name: '',
    description: '',
    date: '625 - 630',
    location: '1'
  }
]);

// 
const stallRecruitment = ref({
  description: '',
  benefits: [
    '',
    '',
    ''
  ]
});

// 
const partTimeJobs = ref([
  {
    title: '',
    salary: '18/',
    tags: ['', ''],
    description: '',
    requirements: [
      '18-35',
      '',
      '',
      ''
    ],
    location: '2',
    time: '',
    benefits: '++'
  },
  {
    title: '',
    salary: '16/',
    tags: [''],
    description: '',
    requirements: [
      '18-40',
      '',
      '',
      ''
    ],
    location: '5',
    time: '',
    benefits: '+'
  },
  {
    title: '',
    salary: '17/',
    tags: [''],
    description: '',
    requirements: [
      '20-35',
      '',
      '',
      ''
    ],
    location: '3',
    time: '',
    benefits: '+'
  },
  {
    title: '',
    salary: '15/',
    tags: [''],
    description: '',
    requirements: [
      '',
      '',
      '',
      ''
    ],
    location: '',
    time: '',
    benefits: '+'
  }
]);

// 
const activeTab = ref('all');

// 
const showBannerModal = ref(false);
const showNotificationModal = ref(false);
const showEventModal = ref(false);
const showStallModal = ref(false);
const showJobModal = ref(false);
const showSuccessModal = ref(false);

// 
const currentBanner = ref({});
const currentNotification = ref({});
const currentEvent = ref({});
const currentJob = ref({});

// 
const successMessage = ref({
  title: '',
  content: ''
});

// 
const stallForm = ref({
  name: '',
  phone: '',
  type: '',
  stallName: '',
  description: '',
  duration: '',
  image: null
});

// 
const fileInput = ref(null);

// 
const startBannerTimer = () => {
  bannerTimer = setInterval(() => {
    nextBanner();
  }, 5000);
};

const nextBanner = () => {
  currentBannerIndex.value = (currentBannerIndex.value + 1) % banners.value.length;
};

const goToBanner = (index) => {
  currentBannerIndex.value = index;
};

// 
const filteredNotifications = computed(() => {
  if (activeTab.value === 'all') return notifications.value;
  return notifications.value.filter(item => item.type === activeTab.value);
});

// 
const switchTab = (tabValue) => {
  activeTab.value = tabValue;
};

// 
const markAsRead = (id) => {
  const item = notifications.value.find(i => i.id === id);
  if (item) item.read = true;
};

// 
const loadMore = () => {
  successMessage.value = {
    title: '',
    content: ''
  };
  showSuccessModal.value = true;
};

// 
const viewDetails = (type, index) => {
  if (type === 'event') {
    openEventDetail(mallEvents.value[index]);
  } else if (type === 'job') {
    openJobDetail(partTimeJobs.value[index]);
  }
};

// 
const viewMoreEvents = () => {
  successMessage.value = {
    title: '',
    content: ''
  };
  showSuccessModal.value = true;
};

// 
const viewMoreJobs = () => {
  successMessage.value = {
    title: '',
    content: ''
  };
  showSuccessModal.value = true;
};

// 
const openBannerDetail = (banner) => {
  currentBanner.value = banner;
  showBannerModal.value = true;
};

const closeBannerModal = () => {
  showBannerModal.value = false;
};

const openNotificationDetail = (notification) => {
  currentNotification.value = notification;
  markAsRead(notification.id);
  showNotificationModal.value = true;
};

const closeNotificationModal = () => {
  showNotificationModal.value = false;
};

const openEventDetail = (event) => {
  currentEvent.value = event;
  showEventModal.value = true;
};

const closeEventModal = () => {
  showEventModal.value = false;
};

const openServiceDetail = (service) => {
  successMessage.value = {
    title: service.name,
    content: service.description + '\n\n' + service.date + '\n' + service.location
  };
  showSuccessModal.value = true;
};

const openStallApplication = () => {
  showStallModal.value = true;
};

const closeStallModal = () => {
  showStallModal.value = false;
  // 
  stallForm.value = {
    name: '',
    phone: '',
    type: '',
    stallName: '',
    description: '',
    duration: '',
    image: null
  };
};

const triggerFileInput = () => {
  fileInput.value.click();
};

const handleFileUpload = (event) => {
  if (event.target.files.length > 0) {
    stallForm.value.image = event.target.files[0];
  }
};

const submitStallApplication = () => {
  // 
  console.log(':', stallForm.value);
  
  // 
  closeStallModal();
  
  // 
  successMessage.value = {
    title: '',
    content: '3'
  };
  showSuccessModal.value = true;
};

const openJobDetail = (job) => {
  currentJob.value = job;
  showJobModal.value = true;
};

const closeJobModal = () => {
  showJobModal.value = false;
};

const openJobApplication = () => {
  closeJobModal();
  successMessage.value = {
    title: '',
    content: 'hr@mall.com"-' + currentJob.value.title + '"'
  };
  showSuccessModal.value = true;
};

const closeSuccessModal = () => {
  showSuccessModal.value = false;
};

// 
const formatDate = (dateString) => {
  const date = new Date(dateString);
  return date.toLocaleDateString('zh-CN');
};

// 
const reset = () => {
  // 
  showBannerModal.value = false;
  showNotificationModal.value = false;
  showEventModal.value = false;
  showStallModal.value = false;
  showJobModal.value = false;
  showSuccessModal.value = false;
  
  // 
  const slideContent = document.querySelector('.slide-content');
  if (slideContent) {
    slideContent.scrollTop = 0;
  }
};

// 
onMounted(() => {
  startBannerTimer();
});

onUnmounted(() => {
  if (bannerTimer) clearInterval(bannerTimer);
});

// 
defineExpose({ reset });
</script>

<style scoped>
.slide {
  flex: 0 0 100%;
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.slide-bg {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  transition: transform 8s ease;
}


/*  */
.slide-core-value .slide-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(135deg, rgba(10, 30, 80, 0.9) 0%, rgba(20, 60, 150, 0.8) 100%),
    radial-gradient(circle at 70% 60%, rgba(50, 120, 255, 0.3) 0%, transparent 60%);
}

/*  */
.tech-decoration {
  position: absolute;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  filter: blur(100px);
  z-index: 1;
}

.tech-decoration.left {
  top: 30%;
  left: -100px;
  background: rgba(50, 150, 255, 0.2);
}

.tech-decoration.right {
  bottom: -50px;
  right: 10%;
  background: rgba(100, 200, 255, 0.15);
}

/*  -  */
.slide-content {
  position: absolute;
  top: 43%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  width: 90%;
  max-width: 1590px;
  padding: 20px;
  z-index: 2;
  opacity: 0;
  animation: fadeIn 1.2s forwards 0.3s;
  height: 740px;
  overflow-y: auto; /*  */
  /*  */
  scrollbar-width: 6px; /* Firefox */
  scrollbar-color: rgba(100, 243, 255, 0.5) transparent; /* Firefox */
}

/* Chrome/Safari  */
.slide-content::-webkit-scrollbar {
  width: 6px; /*  */
}

.slide-content::-webkit-scrollbar-track {
  background: transparent; /*  */
  border-radius: 3px;
}

.slide-content::-webkit-scrollbar-thumb {
  background-color: rgba(100, 243, 255, 0.5); /*  */
  border-radius: 3px;
  transition: background-color 0.3s;
}

.slide-content::-webkit-scrollbar-thumb:hover {
  background-color: rgba(100, 243, 255, 0.8);
}

@keyframes fadeIn {
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

/*  */
.notification-container {
  width: 100%;
  padding: 0;
  margin: 0 0 30px 0;
  background: transparent;
}

/*  */
.recommended-banner {
  position: relative;
  width: 100%;
  height: 280px;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 30px;
  background: 
    linear-gradient(135deg, rgba(10, 30, 80, 0.7) 0%, rgba(20, 60, 150, 0.6) 100%),
    radial-gradient(circle at 70% 60%, rgba(50, 120, 255, 0.2) 0%, transparent 60%);
}

/*  */
.banner-tech-decoration {
  position: absolute;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  filter: blur(80px);
  z-index: 1;
}

.banner-tech-decoration.left {
  top: 20%;
  left: -80px;
  background: rgba(50, 150, 255, 0.15);
}

.banner-tech-decoration.right {
  bottom: -40px;
  right: 5%;
  background: rgba(100, 200, 255, 0.1);
}

.banner-slides {
  display: flex;
  height: 100%;
  transition: transform 0.5s ease;
}

.banner-slide {
  position: relative;
  min-width: 100%;
  height: 100%;
  padding: 0 30px;
  box-sizing: border-box;
}

.banner-content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  max-width: 70%;
  z-index: 2;
}

.banner-tag {
  display: inline-block;
  font-size: 0.85rem;
  padding: 3px 10px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  margin-bottom: 10px;
  backdrop-filter: blur(4px);
}

.banner-title {
  font-size: 1.8rem;
  margin: 0 0 10px 0;
  font-weight: 800;
  text-shadow: 
    0 0 10px rgba(255, 255, 255, 0.8),
    0 0 20px rgba(100, 180, 255, 0.6);
}

.banner-desc {
  font-size: 1rem;
  margin: 0 0 15px 0;
  opacity: 0.9;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

.banner-btn {
  padding: 8px 20px;
  background-color: rgba(255, 255, 255, 0.15);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.3s;
  backdrop-filter: blur(8px);
}

.banner-btn:hover {
  background-color: rgba(255, 255, 255, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(50, 150, 255, 0.2);
}

.banner-icon {
  position: absolute;
  right: 120px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 6rem;
  opacity: 0.2;
  z-index: 2;
}

.banner-next {
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.15);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
  backdrop-filter: blur(8px);
  z-index: 2;
}

.banner-next:hover {
  background-color: rgba(255, 255, 255, 0.25);
  transform: translateY(-50%) scale(1.05);
  box-shadow: 0 5px 15px rgba(50, 150, 255, 0.2);
}

.banner-dots {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 8px;
  z-index: 2;
}

.banner-dots span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: all 0.3s;
}

.banner-dots span.active {
  width: 24px;
  border-radius: 4px;
  background-color: white;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.8);
}

/*  */
.notification-section {
