<template>
  <div class="satisfaction-bar-chart">
    <h4>{{ title }}</h4>
    <!--  -->
    <div ref="chartContainer" class="chart-container"></div>
  </div>
</template>

<script setup>
import { defineProps, ref, onMounted, onUnmounted, watch } from 'vue';
import * as echarts from 'echarts'; // ECharts

// 
const props = defineProps({
  title: {
    type: String,
    default: ''
  },
  data: {
    type: Array,
    default: () => [
      { label: '', value: 85 },
      { label: '', value: 92 },
      { label: '', value: 96 },
      { label: '', value: 90 }
    ]
  }
});

// 
const chartContainer = ref(null);
let chartInstance = null;

// 
const initChart = () => {
  // 
  if (!chartContainer.value) return;

  // 
  if (chartInstance) {
    echarts.dispose(chartInstance);
  }

  // 
  chartInstance = echarts.init(chartContainer.value);

  // 
  const option = getChartOption();
  chartInstance.setOption(option);
};

// 
const getChartOption = () => {
  const labels = props.data.map(item => item.label);
  const values = props.data.map(item => item.value);

  return {
    // 
    backgroundColor: 'rgba(0, 119, 255, 0.05)',
    // 
    tooltip: {
      trigger: 'axis',
      axisPointer: { type: 'shadow' },
      backgroundColor: 'rgba(10, 30, 80, 0.9)',
      borderColor: 'rgba(100, 243, 255, 0.3)',
      borderWidth: 1,
      textStyle: { color: '#fff' },
      formatter: '{a} <br/>{b}: {c}%'
    },
    // 
    grid: {
      left: '5%',
      right: '5%',
      bottom: '10%',
      top: '10%',
      containLabel: true
    },
    // X
    xAxis: [
      {
        type: 'category',
        data: labels,
        axisLine: { lineStyle: { color: 'rgba(255, 255, 255, 0.1)' } },
        axisTick: { show: false },
        axisLabel: {
          color: 'rgba(255, 255, 255, 0.8)',
          fontSize: 12
        }
      }
    ],
    // Y
    yAxis: [
      {
        type: 'value',
        min: 0,
        max: 100,
        interval: 20,
        axisLine: { show: false },
        axisTick: { show: false },
        splitLine: { lineStyle: { color: 'rgba(255, 255, 255, 0.05)' } },
        axisLabel: {
          color: 'rgba(255, 255, 255, 0.6)',
          formatter: '{value}%'
        }
      }
    ],
    // 
    series: [
      {
        name: '',
        type: 'bar',
        data: values,
        barWidth: 35,
        itemStyle: {
          // 
          color: function(params) {
            const colorList = [
              { from: '#0077ff', to: '#00f0ff' },
              { from: '#0091ff', to: '#40bfff' },
              { from: '#00aaff', to: '#80d4ff' },
              { from: '#0088ff', to: '#b3e5ff' }
            ];
            const color = colorList[params.dataIndex] || colorList[0];
            return new echarts.graphic.LinearGradient(0, 1, 0, 0, [
              { offset: 0, color: color.from },
              { offset: 1, color: color.to }
            ]);
          },
          borderRadius: [6, 6, 4, 4],
          shadowColor: 'rgba(0, 240, 255, 0.5)',
          shadowBlur: 10,
          shadowOffsetY: 5
        },
        // 
        label: {
          show: true,
          position: 'top',
          color: '#64f3ff',
          fontSize: 12,
          fontWeight: 'bold',
          formatter: '{c}%'
        },
        // 
        animationDuration: 1500,
        animationEasing: 'cubicOut'
      }
    ]
  };
};

// 
watch(
  () => props.data,
  () => {
    if (chartInstance) {
      chartInstance.setOption(getChartOption());
    }
  },
  { deep: true }
);

// 
const handleResize = () => {
  if (chartInstance) {
    chartInstance.resize();
  }
};

// 
onMounted(() => {
  initChart();
  window.addEventListener('resize', handleResize);
});

// 
onUnmounted(() => {
  if (chartInstance) {
    echarts.dispose(chartInstance);
    chartInstance = null;
  }
  window.removeEventListener('resize', handleResize);
});
</script>

<style scoped>
.satisfaction-bar-chart {
  width: 100%;
  padding: 20px;
  /* background: rgba(0, 119, 255, 0.05); */
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(5px);
}

.satisfaction-bar-chart h4 {
  color: #8ab4f8;
  margin-bottom: 20px;
  font-size: 1.2rem;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(100, 243, 255, 0.1);
}

.chart-container {
  width: 100%;
  height: 260px; /*  */
}

/*  */
@media (max-width: 768px) {
  .chart-container {
    height: 250px;
  }
}
</style>